@property --color {
  syntax: '<linear-gradient>';
  inherits: false;
  initial-value: linear-gradient(120deg, red, orange); /* Correct linear-gradient function */
}

  
  
  
  .checkbtn {
    font-size: 30px;
    color: white;
    background: none;
    border: none;
    position: fixed;
    top: 20px;
    right: 20px;
    z-index: 1000;
    cursor: pointer;
    display: none;

  }
  
  #check {
    display: none;
  }
  nav {

    background-color: black;
    color: white;
    padding: 0px 20px 1px 1%;

  }
  nav ul {
    font-size: large;
    list-style: none;
    background-color: transparent;
    margin-top: 0;
    margin-left: 53%;
    box-sizing: border-box;
    font-weight: bolder;
    font-family: 'Gill Sans', 'Gill Sans MT', Calibri, 'Trebuchet MS', sans-serif;
    display: flex; 
  }
  
  nav li {
    padding: 3%;
    display: inline-block;
  }
  
  nav a {
    position: relative;
    text-decoration: none;
    display: inline-block;
    padding: 0.5em 1em;
    color: aliceblue;
    z-index: 1;
    background-color: black;
    border-radius: 10%;
    box-shadow: 10px 0px 20px rgb(241, 5, 5);
  }
  
  nav a:hover {
    box-shadow: none;
  }
  
  
  nav span {
    padding: 4.5% 6% 0 0;
    position: absolute;
    background-image: conic-gradient(rgb(5, 5, 5), rgb(248, 10, 10), rgb(5, 5, 5), rgb(253, 47, 0));
    border-radius: 5px;
    top: 0%;
    left: 56%;
    transition: .5s;
    z-index: 0;
  }
  
  nav li:nth-child(1):hover~span {
    left: 56%;
  }
  
  nav li:nth-child(2):hover~span {
    margin-left: 7%;
    padding-left: 2%;
    opacity: 1;
  }
  
  nav li:nth-child(3):hover~span {
    margin-left: 16.6%;
    padding-left: 2%;
    opacity: 1;
  }
  
  nav li:nth-child(4):hover~span {
    margin-left: 26.1%;
    padding-left: 2%;
    opacity: 1;
  }
  
  nav li:nth-child(5):hover~span {
    margin-left: 35%;
    padding-left: 2%;
    opacity: 1;
  }
  

  @media screen and (max-width: 1024px) {
    #check:checked ~ ul {
      margin-left: 10%;
      margin-top: 0%;
      padding: 0% 50% 8%;
      background: linear-gradient(120deg, rgb(117, 146, 150), rgb(55, 60, 75));
      position: fixed;
      background-size: 30px;
      height:100vh;
      overflow: hidden;

    }
  
    .checkbtn {
      display: block;
      position: fixed;
      margin-left: 90%;
      background: linear-gradient(45deg,black, rgb(254, 254, 254), rgb(5, 245, 137),rgb(0,0,0));
    
      -webkit-background-clip: text; 
      color: transparent;
      
      padding: 3%;
    }
  
    nav ul {
      text-align: center;
      margin-left: 30%;
      margin-top: 50%;
      padding: 0% 8% 8%;
      background: linear-gradient(120deg, rgb(117, 146, 150), rgb(55, 60, 75));
      position: fixed;
      background-size: 30px 30px;
      display: none; 
    }
  
    nav li {
      padding: 50% 0 0 0;
      display: block;
     
    }
  
    nav a {
      text-decoration: none;
      
      color: aliceblue;
      background-color: black;
      border-radius: 10%;
      box-shadow: none;
      display: block;
      margin-top: 35%;
      left: -70%;
      min-width: 100px;
      padding: 20%;
      transition: all .5s;
    }
      nav a:hover {
        box-shadow: 10px 0px 20px rgb(9, 243, 231) ;
      }
      
    
  
    nav span {
      display: none;
    }

    #check:checked ~ ul {
      display: block;
    }
    #h{
      display: none;
    }
  }
  